/*-------cart page-----*/
.cart-page{
    display: grid;
    grid-template-columns: 60% 40%;
    margin: 1rem;
    margin-left: 3rem;
}

.cart-items{
    display: flex;
    flex-direction: column;
}

.order{
    padding: 1rem;
    display: flex;
    flex-direction: column;
    background-color: cyan;
    position: fixed;
    bottom: 0;
    right: 0;
    height: 80%;
    width: 20%;
    margin-right: 3rem;
    margin-bottom: 1rem;
}

.order button{
    position: fixed;
    bottom: 0;
    width: 15%;
    height: 60px;
    margin: 2rem;
}




/*-----Cart Item-----*/
.cart-item{
    display: grid;
    grid-template-rows: 1fr 1fr;
    grid-template-columns: repeat(3, 1fr);
    float: left;
    margin-top: 1rem;
    background-color: grey;
    color: white;
    padding: 1rem;
}

.cart-item p{
    grid-row: 1 / 2;
    grid-column: 1 / -1;
}

.cart-item button{
    grid-column: 3 / -1;
        width: 48%;
    height: 100%;
    margin: 1px;
    justify-self: flex-end;
}



/*---checkout ------*/
.checkout-container, .checkout-input{
    display: flex;
    flex-direction: column;
   
}

.checkout-container{
    margin:auto;
    width: 40%;
    margin-top:10%;
    background-color: rgb(178, 192, 204);
     padding: 1rem;
    border-radius: 10px;
    

}

.checkout-input input{
    margin-bottom: 1rem !important;
     padding:4px;
     outline: none;
   
}

.order-placed{
    background-color: green;
    color: white;
    margin: auto;
    max-width: 200px;
    height: 200px;
    margin-top: 2rem;
    display: flex;
    align-items: center;
    text-align: center;
}

.order-placed p{
    width: 100%;
    text-align: center;
    font-size: 3rem;
}






