#nav-bar
{
    position: sticky;
    top: 0;
    z-index: 10;
   
}
.navbar{
    padding-top:0 !important;
    padding-bottom: 0 !important;
}

.navbar-brand img
{
    height: 40px;
    padding-left: 30px;
}
.navbar-nav li 
{
    padding: 0 10px;
}
.navbar-nav  li a
{
    float: right;
    text-align: left;
}
#nav-bar ul li a:hover, #btn-logout:hover
{
    color: #007bff!important;
}

.navbar-toggler
{
    border: none!important;
} 
.nav-link
{     color: white !important;
     font-weight: 600;
    font-size: 16px;
    
}

.active{
    color: #007bff !important;
}

#btn-logout{
    font: inherit;
    color: white;
    background: transparent;
    border: none;
    outline: none;
}