.dabba
{
    position: relative;
    width: 1200px;
    height: fit-content;
    margin:auto;
    font-family: sans-serif;
    cursor: pointer;
}
.dabba .box
{
    position: relative;
    width: calc(400px - 30px);
    height: calc(300px - 30px);
    background: #000;
    float: left;
    margin: 15px;
    box-sizing: border-box;
    overflow: hidden;
    border-radius: 10px;
}
.dabba .box .icon
{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #f00;
    transition: 0.5s;
    z-index: 1;
}
.dabba .box:hover .icon
{
    top: 20px;
    left: calc(50% - 40px);
    width: 80px;
    height: 80px;
    border-radius: 50%;
}
.dabba .box .icon .fa
{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    font-size: 80px;
    transition: 0.5s;
    color: #fff; 
}
.dabba .box:hover .icon .fa
{
    font-size: 40px;
}
.dabba .box .content
{
    position: absolute;
    top: 100%;
    height: calc(100% - 100px);
    text-align: center;
    padding: 20px;
    box-sizing: border-box;
    transition: 0.5s;
    opacity: 0;
    width: 100%;
}
.dabba .box:hover .content
{
    top: 100px;
    opacity: 1;
    
}
.dabba .box .content h3
{
    margin: 0;
    padding: 0;
    color: #fff;
    text-align: center;
}
.dabba .box .content p
{
    margin: 0;
    padding: 0;
    color: #fff;
}

.content, .content h3, .content p, .icon, .fa{
    pointer-events: none;
}

