*
{
    margin: 0;
    padding: 0;
}

.carousel-inner{
    top:0 !important;
    height: fit-content;
}



.carousel-caption
{
    top: 40%;
    transform: translateY(-50%);
    bottom: initial!important;
}
.carousel-caption h5
{
    color: #fff!important;
    font-size: 50px!important;
    font-family: serif;
    
}

.d-block{
    height: calc(100vh - 50px) !important;
}







