/*-----------AdminRequests Page--------*/

.request-box *{
    margin: 0;
    padding-left: 0.5rem;
    padding-top: 0.2rem;
}

.request-box{
    display: grid;
    grid-template-rows: 1fr 1fr;
    grid-template-columns: 2fr 0.5fr;
    margin: 1rem;
    border: 2px grey solid;
}
.request-box button{
    grid-column: 2 / -1;
    grid-row: 1 / -1;
}


/*---------Admin Request-----------*/

.request-box *{
    margin: 0;
    padding-left: 0.5rem;
    padding-top: 0.2rem;
}

.request-box{
    display: grid;
    grid-template-rows: 1fr 1fr;
    grid-template-columns: 2fr 0.5fr;
    margin: 1rem;
    border: 2px grey solid;
}
.request-box button{
    grid-column: 2 / -1;
    grid-row: 1 / -1;
}


.requests-selected{
   
    display: grid;
    margin:auto;
    max-width: 700px;
    grid-template-columns: 350px 350px;
    grid-template-rows: auto 50px;
    margin-top: 20px;
}
.req-book-details{
    display: flex;
    flex-direction: column;
}

.control-btns{
    grid-row : 2 / -1;
    grid-column: 1 / -1;
    display: grid;
    grid-template-columns: repeat(3,1fr);
}


