.selected-book{
   display: grid;
   grid-template-rows: 300px auto;
   grid-template-columns: 1fr 1fr;
   max-width: 70%;
   margin: auto;
   margin-top: 10%;
}

.des{
    grid-row: 1 / 2;
    grid-column: 1 / -1;
    display: flex;
}

.des img{
    float: left;
}

.des p{
    padding: 1em;
    text-justify: newspaper;
}

.book-details{
    display :flex;
    flex-direction: column;
    grid-row: 2 / -1;
    grid-column: 1 / 2;
}

.book-details-btn{
    grid-row: 2 / -1;
    grid-column: 2 / -1;
    display: grid;
    grid-template-rows: 1fr;
    grid-template-columns: repeat(2, 1fr);
}

.book-details-btn button{
     background: #ffbf00;
    padding: 10px 20px;
    border-radius: 10%;
    outline: none;
    
}
